@import "~react-image-gallery/styles/css/image-gallery.css";


@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src:
    local('Raleway'),
    local('Raleway-Regular'),
    url(./fonts/Raleway-Regular.ttf) format('truetype');
}

body {
	font-family: 'Raleway', sans-serif;
	background-color: #f7f7f7;

}

heading {
  font-weight: 700;
}
subheading {
  font-weight: 400;
}

App {
  text-align: center;
}

App-logo {
  height: 40vmin;
  pointer-events: none;
}

.logo { 
  text-align: center;
  display: block;
  height: 60%;
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

a {
  color: #e08a8b;
}

.bio {
	margin: 16px 0;
	width: 50%;
	font-size: 4vw;
}

.socialMedia {
	float: right;
}

.socialMedia a{
	display: inline-block;
	padding-right: 10px;
}

.slideshowContainer {
	display: block;
	margin-left: auto;
    margin-right: auto;
	width : 70%;
}
.agencies {
	text-align: center;
	display-inline: block;
	padding: 30px;
}

.agencies a {
	text-align: center;
	display-inline: block;
	padding: 30px;
}

h1 {
	font-size: 4vw;
}

h2 {
	font-size: 3vw;
}

h1, h2, p{
	padding-top: 10px;
	text-align: center;
}

.highlights li{
  	margin: 0 auto;
}

.highlightsPhoto {
	padding-top: 50px;
	text-align: center;
}

.funFacts {
	display-inline: block;
	margin-left: 16px;
	margin-right: 16px;
}
.funFacts li {
	width: 90%;
	font-size: 16px;
	
}